@import "../../00-global/_02-colors.scss";
@import "../../01-atoms/02-forms/slider/_slider.scss";

  @font-face {
    font-family: "Jost VF";
    src: url("../../../../assets/fonts/embeds/jost-VF-subset.woff2")
      format("woff2 supports variations"), url("../../../../assets/fonts/embeds/jost-VF-subset.woff2")
      format("woff2-variations");
    font-display: swap;
    font-weight: 100 900;
  }
  @font-face {
    font-family: "Inter VF";
    src: url("../../../../assets/fonts/embeds/Inter.var-subset.woff2")
      format("woff2 supports variations"), url("../../../../assets/fonts/embeds/Inter.var-subset.woff2")
      format("woff2-variations");
    font-display: swap;
    font-weight: 100 900;
    font-style: oblique 0deg 10deg
  }
  @font-face {
    font-family: "Amstelvar VF";
    src: url("../../../../assets/fonts/embeds/AmstelvarAlpha-VF.woff2")
      format("woff2 supports variations"), url("../../../../assets/fonts/embeds/AmstelvarAlpha-VF.woff2")
      format("woff2-variations");
    font-display: swap;
    font-weight: 100 900;
  }
  
  body {
    margin: 0;
    padding: 0;
  }
  
  .range__axis {
    --text-vf-wght: 400;
    --text-vf-wdth: 95;
    --value-tag: 'wght';
    --low-value: 85;
    --current-value: var(--text-vf-wght);
    --high-value: 100;
    background-color: $color-callout-highlight;
    background: radial-gradient(ellipse at bottom right, var(--color-callout-highlight) 0%, var(--color-callout-highlight-secondary) 70%);
    margin-left: auto;
    margin-right: auto;
    .paragraph {
      .text {
        background-color: $color-blue-200;
        background: radial-gradient(ellipse at top left, var(--color-callout-highlight-alt) 0%, var(--color-callout-highlight-alt-secondary) 70%);
        font-family: "Roboto Extremo", Helvetica, sans-serif;
        font-size: 5rem;
        font-weight: var(--text-vf-wght);
        font-stretch: calc( var(--text-vf-wdth) * 1% );
        line-height: 7rem;
        margin: 0 auto 0 auto;
        text-align: center;    
      }
    }
    .controls {
      display: grid;
      grid-template-columns: min-content 1fr min-content;
      margin: 2em auto;
      text-align: center;
      max-width: 20rem;
      padding: 0 0.5rem;
      h3 {
        display: inline;
        font-weight: normal;
        font-family: monospace;
      }
      label {
        float: left;
        height: 1px;
        text-indent: -999em;
        visibility: hidden;
      }
      .slider {
        grid-column: 2/4;
        input {
          width: 100%;
        }
      }
      .value-tag,
      .value-tag2 {
        grid-column: span 3;
        font-weight: bold;
        margin-bottom: 0.5rem;
        text-align: left;
      }
      .value-tag::before {
        counter-reset: tag var(--value-tag);
        content: "'" var(--value-tag) "' range";
        font-family: sans-serif;
      }
      .value-tag2::before {
        counter-reset: tag var(--value-tag2);
        content: "'" var(--value-tag2) "' range";
        font-family: sans-serif;
      }
      .low-value,
      .low-value2 {
        grid-column: 1/2;
        text-align: left;
        width: 2.5rem;
      }
      .low-value::before {
        counter-reset: tag var(--low-value);
        content: counter(tag);
        font-family: sans-serif;
      }
      .low-value2::before {
        counter-reset: tag var(--low-value2);
        content: counter(tag);
        font-family: sans-serif;
      }
      .high-value,
      .high-value2 {
        grid-column: 4/5;
        text-align: right;
        width: 2.5rem;
      }
      .high-value::before {
        counter-reset: tag var(--high-value);
        content: counter(tag);
        font-family: sans-serif;
      }
      .high-value2::before {
        counter-reset: tag var(--high-value2);
        content: counter(tag);
        font-family: sans-serif;
      }
      .current-value,
      .current-value2 {
        grid-column: 2/3;
      }
      .current-value::before {
        counter-reset: tag var(--current-value);
        border: solid 1px black;
        content: counter(tag);
        display: inline-block;
        font-family: sans-serif;
        min-width: 3rem;
        margin: 0.5rem auto 0 auto;
        padding: 0.25rem 0.25rem 0.2rem 0.25rem;
      }
      .current-value2::before {
        counter-reset: tag var(--current-value2);
        border: solid 1px black;
        content: counter(tag);
        display: inline-block;
        font-family: sans-serif;
        min-width: 3rem;
        margin: 0.5rem auto 0 auto;
        padding: 0.25rem 0.25rem 0.2rem 0.25rem;
      }
    }
  }
  .range__axis .code {
    border-top: solid 1px #d8d8d8;
  }
  
  .range__axis code {
    display: block;
    font-size: 1.25rem;
    line-height: 1.5;
    margin: 0 auto;
    max-width: 20rem;
  }
  
  .range__axis code .current-value::before {
    counter-reset: tag var(--current-value);
    content: counter(tag);
    display: inline;
  }
  
  .range__axis code .current-value2::before {
    counter-reset: tag var(--current-value2);
    content: counter(tag);
    display: inline;
  }
  
  .code code.prettyprint {
    --color-text-highlight: transparent;
    border: none !important;
    font-size: 1.125rem;
    padding: 1.5rem !important;
  }
  .code.prettyprint.css {
    position: relative;
  }
   